@import url(https://fonts.googleapis.com/css2?family=Lobster&family=Roboto&display=swap);

body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html{min-height:100%}body{font-family:'Roboto', sans-serif;font-size:18px}button{padding:16px;font-size:16px;background-color:black;color:white;border-radius:8px;cursor:pointer}h3,h4{font-family:'Lobster', cursive;margin-bottom:0}#page-body{max-width:950px;height:100%;margin:auto;padding:25px;padding-top:64px}.page-title{font-family:'Lobster', cursive;font-size:2em;margin-bottom:0px;margin-left:50px;color:#db4292}.projectImg{width:100%;height:300px;object-fit:cover}.grid-container{height:auto;display:grid;grid-template-columns:repeat(auto-fit, minmax(310px, 1fr));grid-gap:1rem;padding:50px}.grid-item{height:467px;margin-top:50px;max-width:400px}.project-title{font-family:'Lobster', cursive;font-size:1.5em;margin-bottom:20px;text-align:left;color:#db4292}.project-desc{margin-bottom:20px;text-align:left;height:auto}.project-icon{font-size:25px}.fa-icon{margin:10px;left:-8px;position:relative}a{color:#000}a:hover{color:#bd2453}nav ul{list-style-type:none;margin:0;padding:0}nav a{text-decoration:none;color:#db4292;font-weight:bold;padding:16px 32px}nav a:hover{color:#f77cd6}nav .desktop-nav{text-align:right;position:relative;top:0;width:100%;background:transparent}.desktop-li{display:inline-block;padding-top:16px;padding-bottom:16px;list-style:none}.res-menu{font-size:30px;vertical-align:middle;text-align:center;color:#000}.burger-btn{width:50px;height:50px;background:transparent;border:none;display:none;position:fixed;top:0;right:30px;z-index:100}.side-menu{display:block;background-color:white;box-shadow:2px 0px 5px rgba(0,0,0,0.5);width:300px;height:100vh;top:0;right:0;position:fixed;z-index:99;-webkit-transform:translateX(100%);transform:translateX(100%);transition:-webkit-transform 0.3s ease-out;transition:transform 0.3s ease-out;transition:transform 0.3s ease-out, -webkit-transform 0.3s ease-out}.side-menu.open{-webkit-transform:translateX(0px);transform:translateX(0px)}.back-drop{position:fixed;width:100%;height:100vh;background:rgba(0,0,0,0.8);top:0;right:0;z-index:98;transition:0.3s ease-out}.mobile-ul{margin-top:50px}.mobile-li{display:block;padding:20px}@media only screen and (max-width: 640px){.burger-btn{display:block}.desktop-nav{display:none}}@media only screen and (min-width: 641px){.side-menu{display:none}.back-drop{display:none}}.home-container{height:auto;max-width:895px}.title-txt{margin-top:17%;font-size:4em;text-align:center;color:#db4292;text-shadow:2px 2px 4px #3b2a32}.sub-title{font-size:2em;text-align:center;color:#db4292;text-shadow:2px 2px 4px #3b2a32}.dkNav{color:#000}.ltNav{color:#000}.square{width:75px;height:25px;padding:15px;text-align:center;margin:0px auto;color:white;border-radius:20px 20px 20px 20px}.switch-off{background-color:#bd2453;color:#fff;text-shadow:0 0 10px #fff,0 0 20px #fff,0 0 30px #f696e0,0 0 40px #f77cd6,0 0 50px #e154b4,0 0 60px #db4292,0 0 70px #bd2453;box-shadow:0px 0px 55px 20px #f77cd6;-webkit-animation:btnGlow 3s ease-in-out infinite alternate;animation:btnGlow 3s ease-in-out infinite alternate}.switch-on{background-color:#db4292;box-shadow:2px 2px 4px #3b2a32}.switch-on:hover{background-color:#f77cd6}.bg-light{position:absolute;top:0;left:0;width:100vw;height:100vh;z-index:-1;transition:background-color .5s ease}.light-off{background-color:black;color:#fff;width:100vw;height:100vh;background-repeat:no-repeat;background-position:center;background-size:cover}.light-on{background-color:white;color:black;background-repeat:no-repeat;background-position:center;background-size:cover;width:100vw;height:100vh}.textGlow{color:#fff;text-shadow:0 0 10px #fff,0 0 20px #fff,0 0 30px #f696e0,0 0 40px #f77cd6,0 0 50px #e154b4,0 0 60px #db4292,0 0 70px #bd2453;-webkit-animation:textGlow 1s ease-in-out infinite;animation:textGlow 1s ease-in-out infinite}@-webkit-keyframes textGlow{from{text-shadow:0 0 10px #fff,0 0 20px #fff,0 0 30px #f696e0,0 0 40px #f77cd6,0 0 50px #e154b4,0 0 60px #db4292,0 0 70px #bd2453}to{text-shadow:0 0 20px #fff,0 0 30px #ff4da6,0 0 40px #ff4da6,0 0 50px #ff4da6,0 0 60px #ff4da6,0 0 70px #ff4da6,0 0 80px #bd2453}}@-webkit-keyframes btnGlow{0%{background-color:#fff}20%{background-color:#f696e0}40%{background-color:#f77cd6}60%{background-color:#e154b4}80%{background-color:#db4292}100%{background-color:#bd2453}}.aboutImg{width:250px;height:250px;border-top-left-radius:50% 50%;border-top-right-radius:50% 50%;border-bottom-right-radius:50% 50%;border-bottom-left-radius:50% 50%;object-fit:cover;display:block;margin-top:75px;margin-bottom:75px;margin-left:auto;margin-right:auto}.icon-container{margin:75px;text-align:center}.about-icon{font-size:40px;padding:10px;color:#000;margin:0 auto}.about-icon:hover{color:#bd2453}.arch-project-img{width:100%;height:200px;object-fit:cover;background-position:10% 50%}.arch-grid-container{display:grid;grid-template-columns:repeat(auto-fit, minmax(220px, 1fr));grid-gap:1rem}footer{width:100%;height:25px;background-color:transparent;text-align:center;position:relative;left:0;bottom:0;height:60px;width:100%;z-index:2}.phantom{display:block;height:30px;width:100%}

